"use client";

import React, { useState } from 'react';

interface CardProps {
  title: string;
  description: string;
  buttonText: string;
  // youtubeUrl: string;
  // webpageUrl: string; 
  }

  //, youtubeUrl, webpageUrl
const Card: React.FC<CardProps> = ({ title, description, buttonText }) => {
  const [selectedOption, setSelectedOption] = useState('');
  const [customUrl, setCustomUrl] = useState('');
  const [urlError, setUrlError] = useState('');

  const handleSetCustomUrl = (url: string) => {
    setCustomUrl(url)
    setUrlError('');
  }

  const handleSelectChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedOption(event.target.value);
    setUrlError('');

    // if (event.target.value === 'youtube') {
    //   // window.open(youtubeUrl, '_blank');
    // } else if (event.target.value === 'webpage') {
    //   window.open(webpageUrl, '_blank');
    // } 
  }

  return (
    <div className="bg-gray-100 dark:bg-gray-800 rounded-lg shadow-md p-6">
      <h2 className="text-xl font-semibold text-gray-800 dark:text-gray-100 mb-2">{title}</h2>
      <p className="text-gray-700 dark:text-gray-300 mb-4">{description}</p>
      <select
        value={selectedOption}
        onChange={handleSelectChange}
        className="block w-50 p-2 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-blue-200"
      >
        <option value="">Select an option</option>
          <option value="youtube">YouTube URL</option>
          <option value="webpage">Web Page URL</option>
      </select>
      <br></br>
      <input
        type="text"
        placeholder="Enter URL"
        value={customUrl}
        onChange={(e) => handleSetCustomUrl(e.target.value)} 
        className="block w-80 p-2 mb-2 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-blue-200"
      />
      {urlError && <p className="text-red-500 text-sm">{urlError}</p>}
      <button className='bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded'>{buttonText}</button>
      

    </div>
  );
};

export default Card;
